/* COVER */

.cover-box {
    width: 100%;
}
.cover {
    max-width: 100%;
    height: auto;
    user-select: none;
    object-fit: contain;
    box-shadow: 0px 0px 20px 1px rgb(0 0 0 / 10%);
}

/* TRACK DETAILS */

.prod {
    color: var(--prodColor);
}

/* CONTAINER SIZING */
.container {
    width: 100% !important;
    max-width: 604px !important;
}
@media (max-width: 1200px) {
    .container {
        max-width: 604px !important;
    }
}


/* FOOTER */

.listen-box img {
    cursor: pointer;
    border: 1px solid var(--helpColor);
    border-radius: 10px;
    padding: 3px 6px;
}
.listen-box img:hover {
    border-color: var(--colorHover);
}
